/* Global Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
}

/* 1. Header Section */
.header {
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  padding: 10px 20px;
}

.logo {
  font-size: 24px;
  font-weight: bold;
  float: left;
}

.nav-bar {
  margin-left: 150px; /* space for logo */
  display: inline-block;
}

.nav-bar a {
  margin: 0 10px;
  text-decoration: none;
  color: #333;
  font-weight: 600;
}

.nav-bar a:hover {
  color: #cc0000;
}

.ticker {
  clear: both;
  margin-top: 10px;
  background-color: #eee;
  padding: 5px 10px;
  white-space: nowrap;
  overflow: hidden;
}

.ticker-item {
  margin-right: 20px;
  font-size: 14px;
  color: #555;
}

/* 2. Main Headline & Featured Story */
.featured-story {
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
  margin: 20px 0;
  padding: 20px;
}

.featured-image {
  flex: 2;
  min-width: 300px;
}

.featured-image img {
  width: 100%;
  height: auto;
}

.featured-content {
  flex: 1;
  min-width: 300px;
  padding: 20px;
}

.featured-content h1 {
  font-size: 32px;
  margin-bottom: 10px;
}

.featured-content p {
  font-size: 16px;
  color: #555;
}

/* 3. Side Column (Secondary News) */
.side-column {
  background-color: #fff;
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #ddd;
}

.side-column h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.side-list {
  list-style-type: disc;
  padding-left: 20px;
}

.side-list li {
  margin-bottom: 5px;
  font-size: 14px;
  color: #333;
}

/* 4. Additional News Items */
.additional-news {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin: 20px 0;
}

.additional-card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
  transition: transform 0.2s;
}

.additional-card:hover {
  transform: translateY(-5px);
}

.additional-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.additional-card h3 {
  font-size: 18px;
  margin: 10px;
}

.additional-card p {
  margin: 0 10px 10px;
  font-size: 14px;
  color: #555;
}

.additional-card a {
  margin: 0 10px 10px;
  display: inline-block;
  color: #007bff;
  text-decoration: none;
}

.additional-card a:hover {
  text-decoration: underline;
}

/* 5. Footer (Taskbar) */
.footer {
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  position: relative;
}

.footer .weather {
  font-size: 14px;
  margin-bottom: 5px;
}

.footer .url-bar {
  background-color: #444;
  padding: 5px;
  margin-bottom: 5px;
  text-align: center;
}

.footer .url-bar a {
  color: #0af;
  text-decoration: none;
  font-weight: bold;
}

.footer .url-bar a:hover {
  text-decoration: underline;
}

.footer .taskbar {
  display: flex;
  justify-content: space-around;
  border-top: 1px solid #555;
  padding-top: 5px;
}

.footer .taskbar-item {
  font-size: 14px;
}
